import { Component, OnInit } from '@angular/core';
import {  IApiLog } from './api-log';
import { ApiLogService } from './api-log.service';
import { conditionallyCreateMapObjectLiteral } from '@angular/compiler/src/render3/view/util';

@Component({
    templateUrl: './api-log.component.html',
    styleUrls: ['./api-log.component.css']
})

export class ApiLogComponent implements OnInit {
    // tslint:disable-next-line: variable-name
    _selectedValue: string;
    // tslint:disable-next-line: variable-name
    _listfilter: string;
    pageTitle = 'Api Error Log';
    logs: string;
    apilogs: string[] = [];
    filteredapilogs: string[] = [];
    distinctapi: string[] = [];
    hidden = true;
    is_disabled: boolean = false;


    selected(event: any): void {
      if(event.target.value===""||event.target.value===undefined){
        this.is_disabled = false;
        alert("Select appropriate dropdown value");
      } else {
        this.is_disabled = true;
        this._selectedValue = event.target.value;
      }
    }

  get listFilter(): string {
      return this._listfilter;
  }
  set listFilter(value: string) {
      this._listfilter = value;
      this.filteredapilogs = this.listFilter ? this.filterData(this.listFilter) : this.apilogs;
  }

    constructor(private apiLogServie: ApiLogService) {}

    filterData(filterby: string): any {
      if(this._selectedValue ==="" || this._selectedValue === undefined) {
        alert("Select appropriate dropdown value");
        return this.filteredapilogs;
      } else {
        return this.filteredapilogs = this.apilogs.filter(item =>
          item[this._selectedValue].toLowerCase().includes((filterby).toLowerCase()));
      }
    }

    ngOnInit() {
      this.apiLogServie.getApiLogs().subscribe({
        next: apilogs => {
          this.logs = apilogs;
          this.apilogs  = this.apiLogServie.getJsonObject(apilogs).
          sort((a: any, b: any) => {
            const dateA = new Date(a.date).getTime();
            const dateB = new Date(b.date).getTime();
            return dateA > dateB ? 1 : -1; });
          this.filteredapilogs = this.apilogs;
          this.distinctapi = this.apiLogServie.distinctapipoint;
          console.log(this.distinctapi);
        },
        error: err => {
          console.log(err.message);
        }
      });
      console.log(this.apilogs[0]);
    }
  }
