    FormsModule,
    HttpClientModule,
    RouterModule.forRoot([
      {path: 'apiLog', component: ApiLogComponent},
      {path: 'welcome', component: WelcomeComponent},
      {path: '', redirectTo: 'apiLog', pathMatch: 'full'}
    ])
