import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { IApiLog } from './api-log';
import { tap, catchError, filter } from 'rxjs/Operators';


@Injectable({
    providedIn: 'root'
})

export class ApiLogService {

    private LogUrl = 'api/logs/theon-api.txt';
    apilogsjson: string[] = [];
    distinctapipoint: string[] = [];

    constructor(private http: HttpClient) {}

    getApiLogs(): Observable<string> {
        return this.http.get<string>(this.LogUrl, { responseType: 'string' as 'json'})
        .pipe(
            tap(),
            catchError(this.handleError)
        );
    }

    private handleError(err: HttpErrorResponse) {
        let errorMessage = '';
        if (err.error instanceof ErrorEvent) {
        errorMessage = `An error occured: ${err.error.message}`;
      } else {
        errorMessage =  `Server returned code: ${err.status}, error message is: ${err.message}`;
      }
        console.error(errorMessage);
        return throwError(errorMessage);
    }

    
    public getJsonObject(data: string): any {
      const currentStr = data.split('\n');
      let apiLastindex;
      for (let i = 0; i < currentStr.length - 1; i++) {
          let start = 3;
          let  flag = 1;
          const currentObj = JSON.parse(currentStr[i]);
          console.log(currentObj);
          this.apilogsjson.push(currentObj);
          while (flag) {
            if (currentObj.Message[start] !== ':') {
              start++;
            } else {
              apiLastindex = start;
              flag = 0;
            }
          }
          currentObj.Api = currentObj.Message.substring(3, apiLastindex);
          this.distinctapipoint.push(currentObj.Api);
          // tslint:disable-next-line: no-shadowed-variable
          this.distinctapipoint = this.distinctapipoint.filter((x, i, a) => x && a.indexOf(x) === i);
        }
      return this.apilogsjson;

    }

}
